// Code to handle install prompt on desktop

const prompt = { install: undefined, update: undefined }
window.addEventListener('beforeinstallprompt', e => {
    // Prevent Chrome 67 and earlier from automatically showing the prompt
    e.preventDefault()
    // Stash the event so it can be triggered later.
    prompt.install = e
})
export const canInstall = () => !!prompt.install,
    install = () => {
        // Show the prompt
        prompt.install.prompt()
        // Wait for the user to respond to the prompt
        prompt.install.userChoice.then(choiceResult => {
            if (choiceResult.outcome === 'accepted') {
                console.log('User accepted the A2HS prompt')
            } else {
                console.log('User dismissed the A2HS prompt')
            }
            window.location.reload()
        })
    },
    hasUpdate = () => !!prompt.update,
    update = () => {
        window.location.reload()
    },
    onUpdate = reg => (prompt.update = reg),
    releaseTag = process.env.RELEASE_TAG,
    publicUrl = process.env.PUBLIC_URL,
    isLocalhost = Boolean(
        window.location.hostname === 'localhost' ||
            // [::1] is the IPv6 localhost address.
            window.location.hostname === '[::1]' ||
            // 127.0.0.0/8 are considered localhost for IPv4.
            window.location.hostname.match(
                /^127(?:\.(?:25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)){3}$/
            )
    ),
    promised = (met, obj) =>
        new Promise((resolve, reject) =>
            obj && obj[met]
                ? obj[met](err => (err ? reject(err) : resolve(obj)))
                : reject(`No such a method: ${met}`)
        ),
    register = (config = { onUpdate }) => {
        if ('serviceWorker' in navigator) {
            if (process.env.NODE_ENV === 'production') {
                // The URL constructor is available in all browsers that support SW.
                const publicUrl = new URL(
                    process.env.PUBLIC_URL,
                    window.location.href
                )
                if (publicUrl.origin !== window.location.origin) {
                    // Our service worker won't work if PUBLIC_URL is on a different origin
                    // from what our page is served on. This might happen if a CDN is used to
                    // serve assets; see https://github.com/facebook/create-react-app/issues/2374
                    console.info(
                        `For development with service worker set PUBLIC_URL to ${window.location.origin}`
                    )
                    return
                }

                window.addEventListener('load', () => {
                    const swUrl = `${process.env.PUBLIC_URL}/worker.js`

                    if (isLocalhost) {
                        // This is running on localhost. Let's check if a service worker still exists or not.
                        checkValidServiceWorker(swUrl, config)

                        // Add some additional logging to localhost, pointing developers to the
                        // service worker/PWA documentation.
                        navigator.serviceWorker.ready.then(() => {
                            console.log(
                                'This web app is being served cache-first by a service ' +
                                    'worker. To learn more, visit https://cra.link/PWA'
                            )
                        })
                    } else {
                        // Is not localhost. Just register service worker
                        registerValidSW(swUrl, config)
                    }
                })
            } else {
                console.warn(
                    `NODE_ENV is not 'production', service worker NOT installed`
                )
            }
        } else {
            console.warn(`Cannot install service worker to this browser.`)
        }
    },
    registerValidSW = (swUrl, config) => {
        navigator.serviceWorker
            .register(swUrl)
            .then(registration => {
                registration.onupdatefound = () => {
                    const installingWorker = registration.installing
                    if (installingWorker == null) {
                        return
                    }
                    installingWorker.onstatechange = () => {
                        if (installingWorker.state === 'installed') {
                            if (navigator.serviceWorker.controller) {
                                // At this point, the updated precached content has been fetched,
                                // but the previous service worker will still serve the older
                                // content until all client tabs are closed.
                                console.log(
                                    'New content is available and will be used when all ' +
                                        'tabs for this page are closed. See https://cra.link/PWA.'
                                )

                                // Execute callback
                                if (config && config.onUpdate) {
                                    config.onUpdate(registration)
                                }
                            } else {
                                // At this point, everything has been precached.
                                // It's the perfect time to display a
                                // "Content is cached for offline use." message.
                                console.log(
                                    'Content is cached for offline use.'
                                )

                                // Execute callback
                                if (config && config.onSuccess) {
                                    config.onSuccess(registration)
                                }
                            }
                        }
                    }
                }
            })
            .catch(error => {
                console.error(
                    'Error during service worker registration:',
                    error
                )
            })
    },
    checkValidServiceWorker = (swUrl, config) => {
        // Check if the service worker can be found. If it can't reload the page.
        fetch(swUrl, {
            headers: { 'Service-Worker': 'script' },
        })
            .then(response => {
                // Ensure service worker exists, and that we really are getting a JS file.
                const contentType = response.headers.get('content-type')
                if (
                    response.status === 404 ||
                    (contentType != null &&
                        contentType.indexOf('javascript') === -1)
                ) {
                    // No service worker found. Probably a different app. Reload the page.
                    navigator.serviceWorker.ready.then(registration => {
                        registration.unregister().then(() => {
                            window.location.reload()
                        })
                    })
                } else {
                    // Service worker found. Proceed as normal.
                    registerValidSW(swUrl, config)
                }
            })
            .catch(() => {
                console.log(
                    'No internet connection found. App is running in offline mode.'
                )
            })
    },
    unregister = () => {
        if ('serviceWorker' in navigator) {
            navigator.serviceWorker.ready
                .then(registration => {
                    registration.unregister()
                })
                .catch(error => {
                    console.error(error.message)
                })
        }
    },
    clearCache = () =>
        navigator.serviceWorker &&
        navigator.serviceWorker.controller &&
        navigator.serviceWorker.controller.postMessage({ type: 'CLEAR_CACHE' })
